.button {
    width: 6rem;
    background-color: rgb(113, 35, 186);
    color: white;
    font-size: 1rem;
    font-weight: bold;
    padding: 0.4rem;
    border: 0.5px solid white;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.2s ease-in;
}

.button:hover,
.button:active {
    color: rgb(113, 35, 186);
    background-color: white;
    border-color: rgb(113, 35, 186);
} 