.form-control {
    display: flex;
    flex-direction: column;
}

.form-control label {
    display: block;
    font-family: inherit;
    font-weight: bold;
    margin-top: 0.4rem;
}

.form-control input {
    width: 95%;
    display: block;
    margin: 0.2rem auto;
    border: 0.5px solid black;
    border-radius: 7px;
    height: 1.5rem;
}

.form-control > div {
    width: 100%;
    margin-bottom: 0.5rem;
}